@import '../../common/brand-colors.scss';

.wp-block-zolo-social-links {
    --zolo-item-grid-gap: 10px;
    --zolo-icon-size: 1em;
    --zolo-button-text-color: #fff;
    --zolo-button-padding: 10px 20px;
    --zolo-button-gap: 10px;
    --zolo-item-border-width: 2px;
    --zolo-item-border-style: solid;
    --zolo-icon-width: 40px;
    --zolo-icon-height: 40px;
    --zolo-button-text-padding: 0 12px;
    --zolo-button-icon-color: #000;

    gap: var(--zolo-item-grid-gap);
    &.flex {
        display: flex;
        flex-wrap: wrap;
    }
    &.grid {
        display: grid;
    }
    .zolo-social-item {
        border-radius: var(--zoloblocks-button-border-radius);
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        text-decoration: none;
        background-color: var(--zoloblocks-brand-color);
        transition: all 0.2s ease;
        svg {
            width: var(--zolo-icon-size);
            height: var(--zolo-icon-size);
            fill: var(--zoloblocks-brand-button-color);
            display: block;
            transition: all 0.2s ease;
        }
        &:hover {
            background-color: var(--zoloblocks-brand-color-hover);
        }
        &.original {
            &.facebook,
            &.facebook-f,
            &.facebook-square,
            &.fb {
                background-color: var(--facebook);
                border-color: var(--facebook);
                &:hover {
                    background-color: var(--facebook-hover);
                }
                .zolo-social-icon {
                    background-color: var(--facebook);
                }
            }
            &.twitter,
            &.twitter-square {
                background-color: var(--twitter);
                border-color: var(--twitter);
                &:hover {
                    background-color: var(--twitter-hover);
                }
                .zolo-social-icon {
                    background-color: var(--twitter);
                }
            }
            &.instagram {
                background-color: var(--instagram);
                border-color: var(--instagram);
                &:hover {
                    background-color: var(--instagram-hover);
                }
                .zolo-social-icon {
                    background-color: var(--instagram);
                }
            }
            &.linkedin,
            &.linkedin-in {
                background-color: var(--linkedin);
                border-color: var(--linkedin);
                &:hover {
                    background-color: var(--linkedin-hover);
                }
                .zolo-social-icon {
                    background-color: var(--linkedin);
                }
            }
            &.behance,
            &.behance-square {
                background-color: var(--behance);
                border-color: var(--behance);
                &:hover {
                    background-color: var(--behance-hover);
                }
                .zolo-social-icon {
                    background-color: var(--behance);
                }
            }
            &.pinterest,
            &.pinterest-p,
            &.pinterest-square {
                background-color: var(--pinterest);
                border-color: var(--pinterest);
                .zolo-social-icon {
                    background-color: var(--pinterest);
                }
                &:hover {
                    background-color: var(--pinterest-hover);
                }
            }
            &.dribbble,
            &.dribbble-square {
                background-color: var(--dribbble);
                border-color: var(--dribbble);
                .zolo-social-icon {
                    background-color: var(--dribbble);
                }
                &:hover {
                    background-color: var(--dribble-hover);
                }
            }
            &.whatsapp,
            &.whatsapp-square {
                background-color: var(--whatsapp);
                border-color: var(--whatsapp);
                .zolo-social-icon {
                    background-color: var(--whatsapp);
                }
                &:hover {
                    background-color: var(--whatsapp-hover);
                }
            }
            &.youtube,
            &.youtube-square {
                background-color: var(--youtube);
                border-color: var(--youtube);
                .zolo-social-icon {
                    background-color: var(--youtube);
                }
                &:hover {
                    background-color: var(--youtube-hover);
                }
            }
            &.tumblr,
            &.tumblr-square {
                background-color: var(--tumblr);
                border-color: var(--tumblr);
                .zolo-social-icon {
                    background-color: var(--tumblr);
                }
                &:hover {
                    background-color: var(--tumblr-hover);
                }
            }
            &.reddit,
            &.reddit-alien,
            &.reddit-square {
                background-color: var(--reddit);
                border-color: var(--reddit);
                .zolo-social-icon {
                    background-color: var(--reddit);
                }
                &:hover {
                    background-color: var(--reddit-hover);
                }
            }
            &.snapchat,
            &.snapchat-ghost,
            &.snapchat-square {
                color: #000;
                background-color: var(--snapchat);
                border-color: var(--snapchat);
                .zolo-social-icon {
                    background-color: var(--snapchat);
                }
                &:hover {
                    background-color: var(--snapchat-hover);
                }
            }
            &.telegram,
            &.telegram-plane,
            &.telegram-square {
                background-color: var(--telegram);
                border-color: var(--telegram);
                .zolo-social-icon {
