                }
            }
            &.vimeo,
            &.vimeo-square {
                background-color: var(--vimeo);
                border-color: var(--vimeo);
                .zolo-social-icon {
                    background-color: var(--vimeo);
                }
                &:hover {
                    background-color: var(--vimeo-hover);
                }
            }
            &.skype,
            &.skype,
            &.skype-square {
                background-color: var(--skype);
                border-color: var(--skype);
                .zolo-social-icon {
                    background-color: var(--skype);
                }
                &:hover {
                    background-color: var(--skype-hover);
                }
            }
            &.spotify,
            &.spotify-square {
                background-color: var(--spotify);
                border-color: var(--spotify);
                .zolo-social-icon {
