                }
                &:hover {
                    background-color: var(--spotify-hover);
                }
            }
            &.soundcloud,
            &.soundcloud-square {
                background-color: var(--soundcloud);
                border-color: var(--soundcloud);
                .zolo-social-icon {
                    background-color: var(--soundcloud);
                }
                &:hover {
                    background-color: var(--soundcloud-hover);
                }
            }
            &.github,
            &.github-alt,
            &.github-square {
                background-color: var(--github);
                border-color: var(--github);
                .zolo-social-icon {
                    background-color: var(--github);
                }
                &:hover {
                    background-color: var(--github-hover);
                }
            }
            &.twitch {
                background-color: var(--twitch);
                border-color: var(--twitch);
                .zolo-social-icon {
